export default{
	data(){
		return{
			isLook: false,
			ifShow: true,
			text: 'password',
			ipt_1:'请输入ID/手机号',
			ipt_2:'请输入登录密码',
			forget: '忘记密码',
			loginWay:'短信验证码登录'
		}
	},
	methods:{
		look(){
			this.isLook = !this.isLook;
			this.text = this.text == 'password' ? 'text' : 'password';
		},
		chooseWay(){
			if(this.loginWay == '短信验证码登录' ){
				this.ipt_1 = '请输入手机号';
				this.ipt_2 = '请输入6位验证码';
				this.loginWay = '密码登录';	
				this.forget = '获取验证码';
				this.ifShow = false;
			}else{
				this.ipt_1 = '请输入ID/手机号';
				this.ipt_2 = '请输入登录密码';
				this.loginWay = '短信验证码登录';	
				this.forget = '忘记密码';
				this.ifShow = true;
			}
		}
	}
}
